<template>
  <div class="day">
    <div class="sun"></div>
    <div class="cloud"></div>
    <div class="cloud small"></div>
    <div class="cloud small left"></div>
  </div>
</template>

<script>
export default {
  name: 'Day'
}
</script>

<style lang="scss" scoped>
  @import '~@/assets/styles/mixin.scss';

  .day {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    animation: run_day 5s infinite linear;
    .sun {
      position: absolute;
      top: getRem(80px);
      left: 0;
      width: getRem(125px);
      height: getRem(125px);
      border-radius: 50%;
      background: rgb(254, 239, 137);
      animation: sun_movement 5s infinite linear;
      z-index: 1;
    }
    .cloud {
      margin: getRem(30px) auto;
      width: getRem(150px);
      height: getRem(70px);
      display: block;
      background: #fff;
      border-radius: getRem(35px);
      box-shadow: inset 0 -0.07rem 0 0 #d7d7d7;
      position: absolute;
      top: getRem(90px);
      left: getRem(50px);
      z-index: 2;
      &:before {
        content: '';
        width: getRem(65px);
        height: getRem(35px);
        display: block;
        background: #fff;
        border-radius: getRem(35px) getRem(35px) 0 0;
        border-bottom: 0;
        position: absolute;
        top: getRem(-31px);
        left: getRem(23px);
      }
    }
    .cloud.small {
      width: getRem(120px);
      height: getRem(50px);
      border-radius: getRem(30px);
      box-shadow: inset 0 getRem(-7px) 0 0 #d7d7d7;
      position: absolute;
      top: getRem(47px);
      left: getRem(195px);
      &:before {
        width: getRem(45px);
        height: getRem(21px);
        top: getRem(-18px);
        border-radius: getRem(40px) getRem(40px) 0 0;
        left: getRem(20px);
      }
    }
    .cloud.small.left {
      top: getRem(35px);
      left: getRem(-84px);
      width: getRem(100px);
      z-index: 0;
    }
  }

  @keyframes run_day {
    0% {
      transform:translate(0%,0);
    }
    50% {
      transform:translate(100%,0);
    }
    50.01% {
      transform:translate(-100%,0);
    }
  }

  @keyframes sun_movement {
    0% {
      top: getRem(80px);
    }
    20% {
      top: getRem(-10px);
    }
    25% {
      top: getRem(-10px);
    }
    40% {
      top: getRem(80px);
    }
  }
</style>
