<template>
  <div>
    <audio loop ref="audio" @canplay="onCanplay">
      <source src="@/assets/audios/RADWIMPS .mp3" type="audio/mp3">
      <source src="@/assets/audios/RADWIMPS .ogg" type="audio/ogg">
      <source src="@/assets/audios/RADWIMPS .wav" type="audio/wav">
    </audio>
    <div class="wave-wrap">
      <lonely-planet ref="wave"/>
    </div>
  </div>
</template>

<script>
import LonelyPlanet from './visual/lonelyPlanet/index';

export default {
  data () {
    return {
      timer: null
    }
  },
  computed: {
    player () {
      return this.$refs.audio;
    },
    wave () {
      return this.$refs.wave;
    }
  },
  components: {
    LonelyPlanet
  },
  methods: {
    play () {
      this.player.play();
      this.startWave();
    },
    stop () {
      this.player.pause();
      this.stopWave();
    },
    onCanplay () {
      this.$emit('canplay');
    },
    stopWave () {
      clearTimeout(this.timer);
      this.timer = null;
    },
    startWave () {
      this.timer = setInterval(() => {
        this.wave.addWave();
      }, 800);
    }
  }
}
</script>

<style lang="scss" scoped>
 .wave-wrap {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
 }
</style>
