<template>
  <div id="app">
    <load-mask v-if="loading"/>
    <bg-music ref="bgMusic" @canplay="canplay"/>
    <div 
      class="wrap" 
      @mouseenter="showBtns"
      @mouseleave="hideBtns">
      <transition name="hide">
        <div 
          class="btns"
          v-show="showOptions"
          @click="trigglePlay">
          <div class="play-btn" v-show="!playing"></div>
          <div class="stop-btn" v-show="playing"></div>
        </div>
      </transition>
      <night/>
      <day/>
      <div class="land">
        <we class="we-wrap"/>
        <ground/>
      </div>
    </div>
    <div class="text">
      <p class="typing">爱你不仅仅是今天，而是往后的每一个日夜！</p>
    </div>
  </div>
</template>

<script>
import Night from '@/components/night';
import Day from '@/components/day';
import We from '@/components/we';
import Ground from '@/components/ground';
import BgMusic from '@/components/bgMusic';
import LoadMask from '@/components/loadMask';

export default {
  name: 'app',
  components: {
    Night,
    Day,
    We,
    Ground,
    BgMusic,
    LoadMask
  },
  data () {
    return {
      playing: false,
      showOptions: false,
      loading: true
    };
  },
  computed: {
    bgMusic () {
      return this.$refs.bgMusic;
    }
  },
  mounted () {
    this.initEvent();
  },
  methods: {
    initEvent () {},
    trigglePlay () {
      if (this.playing) {
        this.bgMusic.stop();
      } else {
        this.bgMusic.play();
      }
      this.playing = !this.playing;
    },
    showBtns () {
      this.showOptions = true;
    },
    hideBtns () {
      this.showOptions = false;
    },
    canplay () {
      this.loading = false;
    }
  }
}
</script>

<style lang="scss">
  @import '~@/assets/styles/mixin.scss';
  .btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .wrap {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: getRem(420px);
    height: getRem(420px);
    border: getRem(10px) solid #fff;
    border-radius: 50%;
    box-shadow: 0 getRem(10px) getRem(10px) 0 rgba(0,0,0,0.3);
    overflow: hidden;
    animation: change_color 5s infinite linear;
    .land {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
    .we-wrap {
      left: 50%;
      bottom: getRem(120px);
    }
    .btns {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(88, 88, 88, 0.3);
      cursor: pointer;
      z-index: 3;
      .play-btn {
        @extend .btn;
        width: 0;
        height: 0;
        border-bottom: getRem(30px) solid transparent;
        border-top: getRem(30px) solid transparent;
        border-left: getRem(50px) solid rgba(255,255,255,0.3);
        border-right: 0 solid transparent;
      }
      .stop-btn {
        @extend .btn;
        width: getRem(50px);
        height: getRem(60px);
        &:before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          display: block;
          width: getRem(15px);
          height: 100%;
          background: rgba(255,255,255,0.3);
        }
        &:after {
          content: '';
          position: absolute;
          right: 0;
          top: 0;
          display: block;
          width: getRem(15px);
          height: 100%;
          background: rgba(255,255,255,0.3);
        }
      }
    }
    .hide-enter, .hide-leave-to {
      opacity: 0;
    }
    .hide-enter-active, .hide-leave-active {
      transition: opacity 0.3s ease;
    }
  }

  .text {
    font-size: getRem(30px);
    position: absolute;
    bottom: getRem(38px);
    left: 50%;
    transform: translateX(-50%);
    color: #925a5a;
    .typing {
      width: 20em;
      white-space: nowrap;
      border-right: 0.02rem solid transparent;
      animation: typing 3.5s steps(20, end), blink-caret .75s step-end infinite;
      overflow: hidden;
    }
  }

  /* 打印效果 */
  @keyframes typing {
    from { width: 0; }
    to { width: 20em; }
  }
  /* 光标闪啊闪 */
  @keyframes blink-caret {
    from, to { box-shadow: 0.01rem 0 0 0 transparent; }
    50% { box-shadow: 0.01rem 0 0 0; }
  }

  @keyframes change_color {
    0% {
      background: rgb(105, 199, 255);
    }
    33.33% {
      background: #3173bd;
    }
    66.66% {
      background: rgb(14, 19, 65);
    }
    100% {
      background: rgb(105, 199, 255);
    }
  }

</style>

