<template>
  <div>
    <canvas class="canvas" ref="canvas" :width="width" :height="height"></canvas>
  </div>
</template>

<script>
import LonelyPlanet from './main';

export default {
  data () {
    return {
      ctx: null,
      wave: null,
      width: 200,
      height: 200
    }
  },
  computed: {
    canvas () {
      return this.$refs.canvas;
    }
  },
  mounted () {
    this.ctx = this.canvas.getContext('2d');
    this.wave = new LonelyPlanet(this.ctx);

    this.update();

    let tid = null, me = this;

    window.addEventListener("resize", function() {
      clearTimeout(tid);
      tid = setTimeout(me.update, 300);
    }, false);

    this.wave.animate();
  },
  methods: {
    update() {
      let rem = window.getComputedStyle(document.documentElement)["fontSize"] || 50;

      this.width = document.documentElement.clientWidth;
      this.height = document.documentElement.clientHeight;
      this.wave.updateCircleOpt({
        x: this.width / 2,
        y: this.height / 2,
        r: 2.1 * parseInt(rem)
      });
    },
    addWave() {
      this.wave.push();
    }
  }
}
</script>
