let uid = 0;

class Circle {
  constructor(ctx, options) {
    this.id = uid++;
    this.ctx = ctx;

    // 圆心坐标
    this.x = options.x;
    this.y = options.y;

    // 半径
    this.r = options.r;
    this.originR = options.r;

    // 颜色
    this.color = options.color || [255, 255, 255];
    this.opacity = 1;

    // 描边宽度
    this.lineW = options.lineW || 5;

    // 小球位置与大小
    this.cAngle = Math.floor(Math.random() * 360);
    this.cR = Math.random() * 3 + 6;

    // 初始化小圆点坐标
    this.initMinCircle();
  }

  getColor() {
    let colorStr = this.color.join(',');
    return `rgba(${colorStr},${this.opacity})`;
  }

  initMinCircle() {
    var angle = this.cAngle,
        radian = getRadian(angle);

    this.cX = this.x + this.r * Math.cos(radian);
    this.cY = this.y - this.r * Math.sin(radian);
  }

  draw() {
    this.drawCircle();
    this.drawBall();

    // 小球角度改变，形成旋转效果
    this.cAngle += 1;
  }

  // 绘制圆环
  drawCircle() {
    let ctx = this.ctx;

    ctx.beginPath();
    ctx.arc(this.x, this.y, this.r, 0, 360);
    this.setStyle();
  }

  // 绘制小球
  drawBall() {
    let ctx = this.ctx;

    ctx.beginPath();
    this.initMinCircle();
    ctx.arc(this.cX, this.cY, this.cR, 0, 360);
    this.setStyle('fill');
  }

  setStyle(mod) {
    let ctx = this.ctx;

    if (mod === 'stroke' || !mod) {
      ctx.lineWidth = this.lineW;
      ctx.strokeStyle = this.getColor();
      ctx.stroke();
    } else {
      ctx.fillStyle = this.getColor();
      ctx.fill();
    }
  }

  destory() {
    this.ctx = null;
  }
}

class LonelyPlanet {
  constructor(ctx, circleOpt) {
    this.ctx = ctx;
    this.run = null;
    this.circles = [];	
    this.circleOpt = circleOpt;
    this.width = circleOpt && circleOpt.x * 2;
    this.height = circleOpt && circleOpt.y * 2;
  }

  push() {
    this.circles.push(new Circle(this.ctx, this.circleOpt));
  }

  updateCircleOpt(options) {
    this.circleOpt = options;
    this.width = options.x * 2;
    this.height = options.y * 2;
    this.ctx.clearRect(0,0,this.width,this.height);
    this.circles = [];
    this.maxR = this.getMaxRadius();
  }

  getMaxRadius() {

    // 以宽高中小的一个作为边界
    let min = Math.min(this.width, this.height);
    return min * 0.9 / 2;
  }

  updateCircles() {
    this.circles = this.circles.filter(item => item.opacity > 0);
  }

  animate() {
    let me = this, ctx = this.ctx;
    this.run = window.requestAnimationFrame(this.animate.bind(me));

    ctx.clearRect(0,0,this.width,this.height);

    if (!this.circles.length) {
      return;
    }

    this.circles.forEach(circle => {

      // 边界与初始圆边缘距离
      let t = me.maxR - circle.originR;

      ctx.beginPath();
      ctx.moveTo(circle.r,0);
      circle.draw();
      circle.r += 0.5;
      circle.opacity = 1 - (circle.r - circle.originR) / t;
    });

    this.updateCircles();
  }
}

function getRadian(angle) {
  return angle * Math.PI / 180;
}

export default LonelyPlanet;